<template>
  <div class="login" id="logins">
    <div class="top-bolck"></div>
    <dl class="admin_login" @keyup.enter="validate">
     <dt>
      <strong>资产端后台管理系统</strong>
      <!-- <em>Management System</em> -->
     </dt>
     <dd class="user_icon">
      <input type="text" placeholder="账号" class="login_txtbx" v-model="user.userName">
     </dd>
     <dd class="pwd_icon">
      <input type="password" placeholder="密码" class="login_txtbx" v-model="user.passWord">
     </dd>
     <dd class="val_icon">
      <div class="checkcode">
        <input type="text" placeholder="验证码" maxlength="4" class="login_txtbx" v-model="writeCode">
        <canvas class="J_codeimg" id="myCanvas" @click="createCode">对不起，您的浏览器不支持canvas，请下载最新版浏览器!</canvas>
      </div>
     </dd>
     <p style="padding:5px;height:20px">
        <input id="checkId" type="checkbox" v-model="selected" style="width:20px;height:20px;float:left;">
        <label for="checkId" style="float:left;height:20px;line-height:20px;padding-left:5px;">存储密码</label>
     </p>
     <dd>
      <input type="button" value="立即登陆" class="submit_btn" @click="validate">
     </dd>
     <dd>
       <p style="color:red">{{promptTxt}}</p>
     </dd>
    </dl>
  </div>
</template>
<script>
import md5 from 'js-md5'
import Particleground from 'particleground-light'
export default {
  name: 'login',
  data () {
    return {
      user: {
        userName: '',
        passWord: ''
      },
      sysCode: '',
      writeCode: '',
      promptTxt: '',
      selected: false
    }
  },
  mounted () {
    let par = new Particleground(document.getElementById('app'), {
      dotColor: '#F5F5F5',
      lineColor: '#F5F5F5'
    })
    par.toString()
    document.querySelector('body').style.background = '#d9d9d9'
    this.createCode()
  },
  destroyed () {
    document.querySelector('canvas').remove()
    document.querySelector('body').style.background = '#ffffff'
  },
  methods: {
    showCheck (a) {
      var c = document.getElementById('myCanvas')
      var ctx = c.getContext('2d')
      ctx.clearRect(0, 0, 1000, 1000)
      ctx.font = "80px 'Microsoft Yahei'"
      ctx.fillText(a, 0, 100)
      ctx.fillStyle = 'black'
    },
    createCode () {
      let firstNum = Math.ceil(Math.random() * 50)
      let secondNum = Math.ceil(Math.random() * 50)

      this.sysCode = firstNum + secondNum
      this.showCheck(firstNum + '+' + secondNum)
    },
    validate () {
      console.log(123)
      var inputCode = Number(this.writeCode)
      if (inputCode.length <= 0) {
        this.promptTxt = '请输入验证码'
        this.createCode()
        return false
      } else if (inputCode !== this.sysCode) { // 验证码错误
        this.promptTxt = '验证码错误'
        this.writeCode = ''
        this.createCode()
        return false
      } else {
        this.user.passWord = md5(this.user.passWord)
        let data = {
          'userName': this.user.userName,
          'userPwd': this.user.passWord
        }
        this.$api.promise({
          method: 'POST',
          url: 'login',
          data: data
        }).then(res => {
          if (res.data.code > 0) {
            console.dir(res.data.data)
            sessionStorage.setItem('user_token', res.data.data.token)  // 保存token
            sessionStorage.setItem('LoginUser', JSON.stringify(res.data.data)) // 保存所有信息
            if (this.selectred) {
              this.$api.setCookie('user', this.user.userName, 7) // 保存帐号到cookie，有效期7天
              this.$api.setCookie('pswd', this.user.passWord, 7) // 保存密码到cookie，有效期7天
            } else {
              this.$api.delCookie('user')
              this.$api.delCookie('pswd')
            }
            this.$router.push({ path: '/index' })
          } else {
            this.$message.error(res.data.desc)
          }
        }).catch((err) => {
          this.fullscreenLoading = false
          this.$message.error('接口异常：' + err)
        })
      }
    }

  }
}
</script>

<style scoped>
  html{
    font-size:50px;
    -webkit-tap-highlight-color:transparent;
    height:100%;
    min-width:320px;
    overflow-x:hidden;
  }
  body{ 
    font-size:.28em;
    line-height:1;
    color:#333;
  }
  button,input,optgroup,select,textarea{
    color:inherit;
    font:inherit;
    margin:0;
  }
  button,html input[type=button],input[type=reset],input[type=submit]{
    -webkit-appearance:button;
    cursor:pointer;
  }
  input,select,button{
    outline:none;
  }
  @-ms-viewport{
    width:device-width;
  }
  button,input,select,textarea{
    font-family:inherit;
    font-size:inherit;
    line-height:inherit;
  }
  p{
    line-height:1.41;
    text-align:justify;
    margin:-.2em 0;
    word-break:break-all;
  }
  ::-webkit-input-placeholder {
    color:#999;
    line-height:inherit;
  } 
  :-moz-placeholder {
    color:#999;
    line-height:inherit;
  } 
  ::-moz-placeholder {
    color:#999;
    line-height:inherit;
  }
  /*other public*/
  .admin_login{
    width:300px;
    height:auto;
    overflow:hidden;
    margin:0 auto 0 auto;
    padding:40px;
    box-shadow:0 -15px 30px #a8abaf;
    border-radius:5px;
  }
  .admin_login dt{
    font-size:20px;
    font-weight:bold;
    text-align:center;
    color:#a8a9ab;
    text-shadow:0 0 1px #a8abaf;
    margin-bottom:15px;
  }
  .admin_login dt strong{
    display:block;
  }
  .admin_login dt em{
    display:block;
    font-size:12px;
    margin-top:8px;
  }
  .admin_login dd{
    margin:5px 0;
    height:42px;
    overflow:hidden;
    position:relative;
  }
  .admin_login dd .login_txtbx{
    font-size:14px;
    height:26px;
    line-height:26px;
    padding:8px 5%;
    width:90%;
    text-indent:2em;
    border:none;
    background:#b9bdbc;
    color:white;
  }
  .admin_login dd .login_txtbx::-webkit-input-placeholder {
    color:#f4f4f4;
    line-height:inherit;
  } 
  .admin_login dd .login_txtbx:-moz-placeholder {
    color:#f4f4f4;
    line-height:inherit;
  } 
  .admin_login dd .login_txtbx::-moz-placeholder {
    color:#f4f4f4;
    line-height:inherit;
  }
  .admin_login dd .login_txtbx:focus{
    background:#848a89;
  }
  .admin_login dd:before{
    font-family:'adminthemesregular';
    position:absolute;
    top:0;
    left:10px;
    height:42px;
    line-height:42px;
    font-size:20px;
    color:#a8a9ab;
  }
  .admin_login dd.user_icon:before{
    content:"u";
  }
  .admin_login dd.pwd_icon:before{
    content:"p";
  }
  .admin_login dd.val_icon:before{
    content:"n";
  }
  .admin_login dd .ver_btn{
    text-align:right;
    border:none;
    color:#f4f4f4;
    height:42px;
    line-height:42px;
    margin:0;
    z-index:1;
    position:relative;
    float:right;
    background:#b9bdbc;
  }
  .admin_login dd .checkcode{
    height:42px;
    background:#fff;
  }
  .admin_login dd .checkcode input{
    width:203px;
    height:36px;
    line-height:36px;
    padding:3px;
    color:white;
    outline:none;
    border:none;
    text-indent:2.8em;
  }
  .admin_login dd .checkcode canvas{
    width:85px;
    height:36px;
    padding:3px;
    z-index:0;
    background:#b9bdbc;
    cursor: pointer;
  }
  .admin_login dd .submit_btn{
    width:100%;
    height:42px;
    border:none;
    font-size:16px;
    background:#a8a9ab;
    color:#f8f8f8;
  }
  .admin_login dd .submit_btn:hover{
    background:#a8a9ab;
    color:#f4f4f4;
  }
  .admin_login dd p{
    color:#a8a9ab;
    font-size:12px;
    text-align:center;
    margin:5px 0;
  }
  /*public:rtContent*/
  .errorTips{
    color:red;
    margin:0 8px;
  }
  .errorTips:before{
    content:"x";
    font-family:'adminthemesregular';
    margin-right:3px;
    font-size:16px;
  }
  .top-bolck{
    height: 200px;
  }
</style>





